.user-menu__search-form {
  display: flex;
  flex-direction: row;
  gap: 23px;
}

.user-menu__search-input {
  display: none;
}

.user-menu__search-input-img {
  position: absolute;
  content: "";
  width: 39px;
  height: 39px;
  padding: 9px;
  top: 0;
  right: 0;
  border-radius: 50%;
  background-color: @title-black;
}

.user-menu__search-button {
  width: 39px;
  height: 39px;
  border-radius: 50%;
  background-color: @title-black;
  border: none;
  padding: 9px;
  &:hover .user-menu__search-button-img, &:focus .user-menu__search-button-img {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active .user-menu__search-button-img {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}

.user-menu__search-close-toggle {
  display: none;
}

.user-menu__search--open {
  position: fixed;
  background-color: @white;
  height: 100vh;
  width: 100%;
  z-index: 5;
  display: block;
  top: 0;
  left: 0;



  .user-menu__search-form {
    position: absolute;
    content: "";
    top: 30%;
    left: 50%;
    transform: translate(-50%, 0);
    // &::after, &::before {
    //   position: absolute;
    //   content: "";
    //   top: -40px;
    //   right: -40px;
    //   background-color: @title-black;
    //   height: 30px;
    //   width: 3px;
    //   // transform: rotate(45deg);
    //   border-radius: 5px;
    // }
    // &::before {
    //   transform: rotate(45deg);
    // }
    // &::after {
    //   transform: rotate(-45deg);
    // }
  }

  .user-menu__search-input {
    display: block;
    width: max-content;
    border-radius: 100px;
    border-color: @title-black;
    padding: 5px 15px;
  }

  .user-menu__search-close-toggle {
    display: block;
    position: absolute;
    content: "";
    top: 20%;
    right: 15%;
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    padding: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;

    &::after, &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 14px;
      background-color: @title-black;
      height: 30px;
      width: 3px;
      border-radius: 5px;
    }
    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
    &:hover, &:focus {
      &::after, &::before {
        opacity: 0.7;
        transition: 0.2s ease-in-out;
      }
    }
    &:active {
      &::after, &::before {
        opacity: 0.5;
        transition: 0.2s ease-in-out;
      }
    }
  }
}
