.main-content__catalog-item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
  @media (min-width:@tablet-size) {
    gap: 20px;
  }
}

.main-content__catalog-item-promo {
  position: absolute;
  content: "";
  display: block;
  top: 10px;
  left: 10px;
  background-color: @title-black;
  color: @white;
  z-index: 2;
  padding: 2px 14px;
  border-radius: 100px;
  font-size: 14px;
  line-height: 140%;
  font-weight: 600;
  text-align: center;
  box-sizing: border-box;

  @media (min-width:@tablet-size) {
    font-size: 16px;
    padding: 8px 14px;
    min-width: 126px;
  }
}

.main-content__catalog-item-section {
  position: relative;
}

.main-content__catalog-item-photo {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 10px;
  @media (min-width:@tablet-size) {
    border-radius: 20px;
  }
}

.main-content__catalog-item-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
}

.main-content__catalog-item-name {
  font-size: 16px;
  line-height: 140%;
  font-weight: 500;
  color: @title-black;
  margin: 0;
  @media (min-width:@tablet-size) {
    font-size: 18px;
  }
}

.main-content__catalog-item-price {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

.main-content__catalog-item-price-standart {
  font-size: 16px;
  line-height: 140%;
  font-weight: 700;
  color: @title-black;
  margin: 0;
  @media (min-width:@tablet-size) {
    font-size: 22px;
  }
}

.main-content__catalog-item-currency {
  font-weight: 600;
}

.main-content__catalog-item-price-special + .main-content__catalog-item-price-standart {
  font-size: 14px;
  font-weight: 600;
  color: @grey;
  text-decoration: line-through;
  @media (min-width:@tablet-size) {
    font-size: 22px;
  }
}

.main-content__catalog-item-price-special {
  color: @orange;
  margin: 0;
  @media (min-width:@tablet-size) {
    font-size: 22px;
  }
}

.main-content__catalog-item-details {
  position: absolute;
  content: "";
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 129px;
  box-sizing: border-box;
  font-style: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 100px;
  background-color: @orange;
  text-align: center;
  color: @white;
  font-size: 14px;
  line-height: 140%;
  font-weight: 600;

  @media (min-width:@tablet-size) {
    padding: 13px;
    font-size: 16px;
    width: 258px;
  }

  &:hover, &:focus {
    color: @grey;
    transition: 0.2s ease-in-out;
  }
  &:active {
    background-color: darken(@orange, 5%);
    transition: 0.2s ease-in-out;
  }
}
