.header-menu--close {
  @media (max-width:@pre-desktop-size) {
    .main-nav__wrapper {
      display: none;
    }
    .user-menu {
      display: none;
    }
    .info-bar {
      display: none;
    }
  }
}

.header-menu--open {
  @media (max-width:@pre-desktop-size) {
    .main-nav {
      width: 100%;
    }
    .main-nav__wrapper {
      z-index: 2;
      background-color: @white;
      width: 100%;
    }
    .main-nav__list {
      display: flex;
      flex-direction: column;
      align-items: center;

    }
    .main-nav__item {
      margin: 10px 0;
    }
    .main-nav__item-link {
      padding: 10px;
    }
    .user-menu {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 15px;
    }
    .info-bar {
      display: block;
      margin-top: 20px;
    }
    .info-bar__wrapper {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        gap: 20px;
    }
    .info-bar__link {

      font-size: 12px;
    }
  }
}
