.page-header {
    width: 100%;
    background-color: @white;
}

.header-menu-wrapper {
  @media (min-width:@desktop-size) {
    display: flex;
    flex-direction: column;
  }
}

.header-menu {
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  min-height: 56px;
  box-sizing: border-box;
  @media (min-width:@desktop-size) {
    // display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
    padding: 12px 0;
    min-height: 80px;
  }
}

.page-header__logo {
  text-decoration: none;
  cursor: pointer;
  display: block;
  &:hover .page-header__logo-img, &:focus .page-header__logo-img {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active .page-header__logo-img {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}

.page-header__logo-img {
  width: 141px;
  height: 36px;
  @media (min-width:@desktop-size) {
    width: 217px;
    height: 56px;
  }
}

// .main-nav {
//   position: relative;
// }

.main-nav__toggle {
  position: absolute;
  content: "";
  right: 35px;
  top: 10px;

  cursor: pointer;
  height: 40px;
  width: 40px;
  background-color: transparent;
  border: none;
  padding: 0;

  @media (min-width:@tablet-size) {
    right: 44px;
  }

  @media (min-width:@desktop-size) {
    display: none;
  }

  &:hover .main-nav__toggle-image, &:focus .main-nav__toggle-image {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active .page-header__logo-img {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }


}

.main-nav__list {
  display: flex;
  list-style: none;
  padding-left: 0;
  @media (min-width:@desktop-size) {
    display: flex;
    flex-direction: row;
    gap: 100px
  }
}

.main-nav__item-link {
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;

  &:hover, &:focus {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }

  @media (min-width:@desktop-size) {
    font-size: 16px;
    font-weight: 600;

  }
}

.main-nav__wrapper {
  position: relative;
  // display: none;
  @media (min-width:@desktop-size) {
    display: block;
  }
}

.user-menu {
  // display: none;
  @media (min-width:@desktop-size) {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 23px;
  }
}

