.user-menu__basket-link {
  background-color: @title-black;
  border-radius: 100px;
  padding: 12px 20px;
  color: @white;
  padding-left: 57px;
  position: relative;
  text-decoration: none;

}

.user-menu__basket-img {
  position: absolute;
  content: "";
  width: 32px;
  height: 32px;
  top: 50%;
  left: 20px;
  transform: translate(0, -50%);
}

.user-menu__basket-goods {
  position: absolute;
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  top: 3px;
  left: 37px;
  background-color: @white;
  color: @dark-black;
  border-radius: 100%;
  text-align: center;
  font-size: 12px;
  font-family: Inter, Arial, sans-serif;
}

.info-bar {
  // display: none;
  background-color: @title-black;
  padding: 8px 0;
  @media (min-width:@desktop-size) {
    display: block;
    order: -1;
    padding: 12px 0;
    background-color: @title-black;
  }
}

.info-bar__wrapper {
  @media (min-width:@desktop-size) {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 70px;
  }
}

.info-bar__link {
  color: @white;
  text-decoration: none;
  &:hover, &:focus {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}

