.page__body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  font-family: Lato, Arial, sans-serif;
  color: #283645;
  font-size: 16px;
  font-weight: normal;
  line-height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  width: auto;
  margin: 0 35px;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 44px;
  }
}
@media (min-width: 1300px) {
  .wrapper {
    width: 1240px;
    margin: 0 auto;
  }
}
