.user-menu__search-form {
  display: flex;
  flex-direction: row;
  gap: 23px;
}
.user-menu__search-input {
  display: none;
}
.user-menu__search-input-img {
  position: absolute;
  content: "";
  width: 39px;
  height: 39px;
  padding: 9px;
  top: 0;
  right: 0;
  border-radius: 50%;
  background-color: #231815;
}
.user-menu__search-button {
  width: 39px;
  height: 39px;
  border-radius: 50%;
  background-color: #231815;
  border: none;
  padding: 9px;
}
.user-menu__search-button:hover .user-menu__search-button-img,
.user-menu__search-button:focus .user-menu__search-button-img {
  opacity: 0.7;
  transition: 0.2s ease-in-out;
}
.user-menu__search-button:active .user-menu__search-button-img {
  opacity: 0.5;
  transition: 0.2s ease-in-out;
}
.user-menu__search-close-toggle {
  display: none;
}
.user-menu__search--open {
  position: fixed;
  background-color: #ffffff;
  height: 100vh;
  width: 100%;
  z-index: 5;
  display: block;
  top: 0;
  left: 0;
}
.user-menu__search--open .user-menu__search-form {
  position: absolute;
  content: "";
  top: 30%;
  left: 50%;
  transform: translate(-50%, 0);
}
.user-menu__search--open .user-menu__search-input {
  display: block;
  width: max-content;
  border-radius: 100px;
  border-color: #231815;
  padding: 5px 15px;
}
.user-menu__search--open .user-menu__search-close-toggle {
  display: block;
  position: absolute;
  content: "";
  top: 20%;
  right: 15%;
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.user-menu__search--open .user-menu__search-close-toggle::after,
.user-menu__search--open .user-menu__search-close-toggle::before {
  position: absolute;
  content: "";
  top: 0;
  left: 14px;
  background-color: #231815;
  height: 30px;
  width: 3px;
  border-radius: 5px;
}
.user-menu__search--open .user-menu__search-close-toggle::before {
  transform: rotate(45deg);
}
.user-menu__search--open .user-menu__search-close-toggle::after {
  transform: rotate(-45deg);
}
.user-menu__search--open .user-menu__search-close-toggle:hover::after,
.user-menu__search--open .user-menu__search-close-toggle:focus::after,
.user-menu__search--open .user-menu__search-close-toggle:hover::before,
.user-menu__search--open .user-menu__search-close-toggle:focus::before {
  opacity: 0.7;
  transition: 0.2s ease-in-out;
}
.user-menu__search--open .user-menu__search-close-toggle:active::after,
.user-menu__search--open .user-menu__search-close-toggle:active::before {
  opacity: 0.5;
  transition: 0.2s ease-in-out;
}
