.main-content {
  padding: 40px 0;
  @media (min-width:@tablet-size) {
    padding: 50px 0;
  }
  @media (min-width:@desktop-size) {
    padding: 60px 0;
  }
}

.main-content__title {
  font-family: Korataki, Arial, sans-serif;
  font-size: 24px;
  line-height: 140%;
  color: @title-black;
  margin: 0;
  margin-bottom: 24px;
  @media (min-width:@tablet-size) {
    font-size: 32px;
    margin-bottom: 40px;
  }
  @media (min-width:@desktop-size) {
    font-size: 40px;
  }
}

.main-content__catalog-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 30px;
  column-gap: 10px;
  width: 100%;
  @media (min-width:@tablet-size) {
    column-gap: 40px;
    row-gap: 50px;
  }
  @media (min-width:@desktop-size) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px;
  }
}


