.page-footer {
  background-color: @light-black;
  padding: 40px 0;
  @media (min-width:@tablet-size) {
    padding: 50px 0;
  }
  @media (min-width:@desktop-size) {
    padding: 64px 0;
  }
}

.footer-wrapper {
  display: grid;
  gap: 35px;
  @media (min-width:@tablet-size) {

  }
  @media (min-width:@desktop-size) {
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto;
    grid-template-areas:
    "logo nav nav nav"
    ". nav nav nav";
    column-gap: 130px;
    position: relative;
  }
}

.page-footer__container-logo {
  @media (min-width:@desktop-size) {
    grid-area: logo;
  }
}

.page-footer__logo {
  text-decoration: none;
  cursor: pointer;
  display: block;

  @media (min-width:@tablet-size) {
  }

  @media (min-width:@desktop-size) {
  }

  &:hover .page-footer__logo-img, &:focus .page-footer__logo-img {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active .page-footer__logo-img {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}

.page-footer__logo-img {
  width: 180px;
  height: 47px;
  @media (min-width:@desktop-size) {
    width: 217px;
    height: 56px;
  }
}

.page-footer__nav {
  display: grid;
  gap: 25px;
  @media (min-width:@tablet-size) {
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;

    grid-template-areas:
    "addres contact services"
    "doc doc services";
    column-gap: 42px;
    row-gap: 0;
  }
  @media (min-width:@desktop-size) {
    grid-area: nav;
    grid-template-areas:
    "addres services doc"
    "contact services .";
    row-gap: 38px;
  }
}

.page-footer__addres {
  display: flex;
  flex-direction: column;
  gap: 10px;
  @media (min-width:@tablet-size) {
    grid-area: addres;
    gap: 20px;
    min-width: 210px;
  }
}

.page-footer__title {
  margin: 0;
  color: @white;
  font-size: 18px;
  font-weight: 700;
  line-height: 100%;
}

.page-footer__text {
  margin: 0;
  color: @white;
  font-size: 16px;
  line-height: 150%;
}

.page-footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 10px;
  @media (min-width:@tablet-size) {
    grid-area: contact;
    gap: 20px;
  }
}

.page-footer__contacts-container {
  display: flex;
  flex-direction: column;
  gap: 25px;
  @media (min-width:@tablet-size) {
    gap: 15px;
  }
}

.page-footer__contacts-link {
  color: inherit;
  font-size: inherit;
  line-height: inherit;
  text-decoration: none;
}

.page-footer__services {
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media (min-width:@tablet-size) {
    grid-area: services;
  }
}

.page-footer__services-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media (min-width:@tablet-size) {
    gap: 30px;
  }
  @media (min-width:@desktop-size) {
    gap: 20px;
  }
}

.page-footer__services-link {
  color: @white;
  font-size: 16px;
  line-height: 100%;
  text-decoration: none;
  &:hover, &:focus {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}

.page-footer__doc {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media (min-width:@tablet-size) {
    grid-area: doc;
  }
  @media (min-width:@desktop-size) {
    align-items: flex-end;
  }
}

.page-footer__doc-link {
  color: @grey;
  font-size: 16px;
  line-height: 140%;
  text-decoration: none;
  &:hover, &:focus {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}

.page-footer__info-container {
  margin-top: 37px;
  display: flex;
  flex-direction: column;
  gap: 30px;

  @media (min-width:@tablet-size) {
    margin-top: 0;
    flex-direction: row;
    justify-content: space-between;
    gap: 85px;
    align-items: center;
  }
  @media (min-width:@desktop-size) {
    gap: 700px;
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;

  }
}

.page-footer__payment {
  @media (min-width:@tablet-size) {
    grid-area: pay;
  }
  @media (min-width:@desktop-size) {
    order: 2;
  }
}

.page-footer__payment-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 33px;
}

.page-footer__copyright {
  @media (min-width:@tablet-size) {
    grid-area: copy;
  }
}

.page-footer__copyright-text {
  font-family: Roboto, Arial, sans-serif;
  margin: 0;
  color: @grey;
  font-size: 16px;
  line-height: 130%;
}

.page-footer__copyright-link {
  color: inherit;
  font-size: inherit;
  line-height: inherit;
  &:hover, &:focus {
    opacity: 0.7;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.5;
    transition: 0.2s ease-in-out;
  }
}
